import * as polyfill from 'setimmediate2';

// Make sure that setTimeout is called every now and then in order to work around
// scheduling issues in chrome.

const enum CONST {
    setTimeoutRecurrence = 10
}

let index = 0;

export function setImmediate(callback: () => void) {
    if (index === 0) {
        setTimeout(callback, 0);
    } else {
        polyfill.setImmediate(callback);
    }

    index = (index + 1) % CONST.setTimeoutRecurrence;
}
